,0,0,0.2);padding:8px;transition:all cubic-bezier(0.4, 0, 1, 1) 0.2s;border:1px solid rgba(255,255,255,0.06);cursor:pointer;overflow:hidden}.social__item:before{content:"";position:absolute;left:-100%;top:0;height:100%;width:100%;background-image:linear-gradient(90deg, transparent, rgba(255,255,255,0.3), transparent);transition:.5s}.social__item:hover:before{left:100%}.social__item:hover{transform:scale(1.1)}.social__link{line-height:0;display:block;width:100%}.social__link .icon{max-width:40px;width:100%}.skill-wrapper{display:flex;justify-content:space-between;flex-wrap:wrap}.skill-col{max-width:33%;flex:0 0 33%;padding:15px;cursor:pointer}.skill-col:hover .skill-card{color:#fff;box-shadow:10px 10px 12px rgba(0,0,0,0.2);transform:translateY(-5px)}.skill-col:hover .skill-card:before{background-position:0 0}.skill-card{position:relative;min-height:unset;padding:15px;text-align:center;background-color:rgba(0,0,0,0.3)}.skill-card:before{content:"";position:absolute;left:0;top:0;height:100%;width:100%;background-image:linear-gradient(45deg, transparent 58%, rgba(255,255,255,0.3) 67%, transparent 68%);background-position:170% 0;background-size:200% 100%;border-radius:4px;transition:.5s}.skill__title{text-transform:uppercase}.resume-wrapper{border-left:3px solid #fff}.resume-icon{vertical-align:middle;margin-right:10px}.resume-single{display:flex;margin-top:30px}.resume-single .summary{flex:0 0 220px;max-width:220px;align-self:flex-start;padding-left:20px;position:relative}.resume-single .summary:before{content:"";position:absolute;left:-9px;top:6px;height:15px;width:15px;border-radius:100px;background:#00c7b5;border:3px solid #fff}.resume-single .year{color:#fff;margin-bottom:0}.resume-single .details{position:relative;padding-left:20px}.resume-single .details:before{content:"";position:absolute;left:0;top:15px;height:1px;width:30px;background:#fff}.resume-single .details h5{color:#00c7b5;margin-bottom:0;font-weight:700;text-shadow:2px 2px 4px rgba(1,1,1,0.3)}.resume-single .details .company{color:#fff}.exp-wrapper{padding:10px 40px 30px 40px}.home_col{max-width:750px;margin:auto;padding:20px 0}.home_col h1{font-size:3rem;text-align:center}.contact-form{padding:30px;position:relative}.contact-form .form-field{margin-top:15px;position:relative}.contact-form .form-field:nth-child(1){margin-top:0}.contact-form .form-field-error{height:17px;margin-left:10px;display:block;color:#ff5a5a;font-size:.8rem;line-height:1.4}.contact-form .form-status{min-height:28px;color:#00c7b5;display:block;position:absolute;right:30px;bottom:30px;font-size:1rem}.contact-form .form-status.error{color:#ec5050}.contact-form .label{position:relative;transition:all .4s ease-out;pointer-events:none;font-size:.94rem}.contact-form .input,.contact-form .textarea{background:rgba(1,1,1,0.2);border:1px solid rgba(255,255,255,0.1);border-radius:4px;font:300 1rem Poppins;color:#fff;vertical-align:middle;width:100%;padding:0 15px;transition:all linear .2s;box-shadow:10px 10px 12px rgba(0,0,0,0.2)}.contact-form .input:focus,.contact-form .textarea:focus{outline:none;background:rgba(1,1,1,0.5);box-shadow:20px 20px 22px rgba(0,0,0,0.2)}.contact-form .input{height:50px}.contact-form .textarea{padding:10px 15px;resize:none}.contact-form .btn{position:relative;padding:0 30px;background:#00c7b5;border:0;color:#fff;border-radius:4px;display:inline-block;z-index:1;text-transform:uppercase;font:.9rem Poppins;letter-spacing:2px;height:50px;line-height:50px;overflow:hidden;transition:all .4s ease-out}.contact-form .btn:not(:disabled){cursor:pointer}.contact-form .btn:not(:disabled):before{content:"";position:absolute;left:-100%;top:0;height:100%;width:100%;background-image:linear-gradient(90deg, transparent, rgba(255,255,255,0.5), transparent);transition:.5s}.contact-form .btn:not(:disabled):hover{color:#fff;box-shadow:10px 10px 12px rgba(0,0,0,0.2);transform:translateX(10px)}.contact-form .btn:not(:disabled):hover:before{left:100%}.contact-form ::-webkit-input-placeholder{color:rgba(255,255,255,0.7)}.contact-form :-ms-input-placeholder{color:rgba(255,255,255,0.7)}.contact-form ::-ms-input-placeholder{color:rgba(255,255,255,0.7)}.contact-form ::placeholder{color:rgba(255,255,255,0.7)}.portfolio__content{border-radius:0 0 8px 8px;padding:15px}.portfolio__content .item-title{color:#fff;margin:1rem 0 0;font-weight:700}.portfolio__content .item-desc{color:#e1e8fb;margin-bottom:0}.portfolio__content .item-stack{font-style:italic;font-size:.75rem;text-align:right;margin-top:.5rem}.portfolio__wrapper{width:100%;cursor:pointer;padding:15px;display:flex;flex-direction:column}.portfolio__wrapper:hover .portfolio__img:before{transform:scaleY(1)}.portfolio__wrapper:hover .portfolio__img a{opacity:1;visibility:visible}.portfolio__img{width:100%;border-radius:8px 8px 0 0;background-image:url(/react-portfolio/static/media/placeholder.420c3468.jpg);background-color:#ffffe6;display:flex;align-items:center;justify-content:center;min-height:200px;background-size:cover;background-position:center;position:relative;overflow:hidden}.portfolio__img a{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);opacity:0;visibility:hidden;transition:.5s;padding:8px 12px;border-radius:50%;color:#00c7b5;background:#fff}.portfolio__img:before{content:"";position:absolute;transform:scaleY(0);height:100%;width:100%;background:rgba(0,199,181,0.3);transition:.4s}.header{display:flex;flex-direction:column;text-align:center;z-index:10;transition:all .4s ease-out;height:calc(100vh - 40px);position:fixed;left:20px;top:20px;transform:translateX(-110%);width:260px;padding:15px 0;background-color:rgba(255,255,255,0.06);-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);border-radius:8px;border:1px solid rgba(255,255,255,0.1);box-shadow:20px 20px 22px rgba(0,0,0,0.2)}.header.is-visible{transform:translateX(0)}.header-inner{display:flex;align-items:center;width:100%;height:100%}.header-toggle{position:absolute;left:100%;top:20px;padding:0;height:50px;width:50px;justify-content:center;align-items:center;display:flex;border:1px solid rgba(255,255,255,0.06);border-left:0;border-radius:0 10px 10px 0;-webkit-backdrop-filter:blur(20px);backdrop-filter:blur(20px);background-color:rgba(255,255,255,0.06);cursor:pointer}.header-toggle:focus{outline:none}.nav{width:100%}.nav-link{text-transform:uppercase;font-size:.94rem;letter-spacing:1px;display:block;padding:5px 0;margin-bottom:1px;color:#ffffff;transition:all .4s ease-out;font-weight:400;position:relative;overflow:hidden;z-index:1}.nav-link:hover{color:#00c7b5}.nav-link:hover:before{width:100%;visibility:visible}.nav-link:before{content:"";position:absolute;top:0;left:0;right:100%;height:100%;width:0;background:rgba(0,199,181,0.2);visibility:hidden;transition:all .4s ease-out;z-index:-1}.nav-link span{display:inline-block}.nav-link--active,.nav-link--active:hover{color:#ffffff}.nav-link--active:before{background:#00c7b5;width:100%;visibility:visible}@media only screen and (max-width: 991px){.resume-single{flex-wrap:wrap}.resume-single .details:before{left:3px;top:-15px;width:12px}}@media only screen and (max-width: 767px){.exp-wrapper{padding:10px 20px 30px 30px}}@media only screen and (max-width: 380px){.home_col h1{font-size:2rem;line-height:2.5rem}.home_col .typing-text{font-size:1rem}}@media (min-width: 768px){.portfolio__wrapper{width:50%}.container{padding:20px 30px}}@media (min-width: 992px){.portfolio__wrapper{flex:0 0 auto;width:33.3333333333%}.resume-single .details{padding-left:50px}.col-6{width:50%}}@media (min-width: 1200px){.header-toggle{display:none}.header{transform:translateX(0)}.wrapper{padding-left:280px}h2{margin-top:0}}

